import React from 'react';
import { Route, Switch } from 'react-router-dom';
import App from '../App';
import Loadable from 'react-loadable';
import Loading from '../components/base/loading';

const ReservationsList = Loadable({ // 我的预约单
  loader: () => import('../pages/reservations-list'),
  loading: Loading,
  timeout: 5000,
  delay: 200
});
const DealerList = Loadable({ // 门店选择列表
  loader: () => import('../pages/dealer-list'),
  loading: Loading,
  timeout: 5000,
  delay: 200
});
const ReservationsDetail = Loadable({ // 预约单详情
  loader: () => import('../pages/reservations-detail'),
  loading: Loading,
  timeout: 5000,
  delay: 200
});
const ProcessTracking = Loadable({ // 服务进程追踪
  loader: () => import('../pages/process-tracking'),
  loading: Loading,
  timeout: 5000,
  delay: 200
});

const BussinessDetail = Loadable({ // 商家详情
  loader: () => import('../pages/business-detail'),
  loading: Loading,
  timeout: 5000,
  delay: 200
});

const ServiceDetail = Loadable({ // 套餐详情
  loader: () => import('../pages/service-detail'),
  loading: Loading,
  timeout: 5000,
  delay: 200
});

const AddCar = Loadable({ // 添加车库 暂时写成页面，估计得弄成弹窗
  loader: () => import('../pages/add-car'),
  loading: Loading,
  timeout: 5000,
  delay: 200
});
const ReservationDetail = Loadable({ // 预约详情
  loader: () => import('../pages/reservation-detail'),
  loading: Loading,
  timeout: 5000,
  delay: 200
});
const ReservationSuccess = Loadable({ // 预约成功
  loader: () => import('../pages/reservation-success'),
  loading: Loading,
  timeout: 5000,
  delay: 200
});

const ServiceReserve = Loadable({ // 套餐预约
  loader: () => import('../pages/service-reserve'),
  loading: Loading,
  timeout: 5000,
  delay: 200
});


const ServiceList = Loadable({ // 预约保养列表
  loader: () => import('../pages/service-list'),
  loading: Loading,
  timeout: 5000,
  delay: 200
});

const ServiceComment = Loadable({ // 评价服务
  loader: () => import('../pages/service-comment'),
  loading: Loading,
  timeout: 5000,
  delay: 200
});

const ServiceInfo = Loadable({ // 透明服务介绍
  loader: () => import('../pages/service-info'),
  loading: Loading,
  timeout: 5000,
  delay: 200
});

const Root = () => {
  return (
    <App>
      <Switch>
        <Route path="/" exact component={ServiceList} />
        <Route path="/reservationsList" component={ReservationsList}/>
        <Route path="/dealerList" component={DealerList}/>
        <Route path="/reservationsDetail" component={ReservationsDetail}/>
        <Route path="/processTracking" component={ProcessTracking}/>
        <Route path="/businessDetail" component={BussinessDetail}/>
        <Route path="/serviceDetail" component={ServiceDetail}/>
        <Route path="/addCar" component={AddCar}/>
        <Route path="/srviceReserve" component={ServiceReserve}/>
        <Route path="/reservationDetail" component={ReservationDetail}/>
        <Route path="/reservationSuccess" component={ReservationSuccess}/>
        <Route path="/serviceList" component={ServiceList}/>
        <Route path="/serviceComment" component={ServiceComment}/>
        <Route path="/serviceInfo" component={ServiceInfo}/>
      </Switch>
    </App>
  );
};
export default Root;
