import api from './client-api';
const version = 'v1_0_0';
const versionV2 = '/v2/api/';
const serviceDomin = window.location.protocol.indexOf('https') !== -1 ? 'https://yczj.api.autohome.com.cn' : 'http://test.yczj.api.autohome.com.cn';

export default {
  /**
   * 获取主软V2协议定位授权
   */
  getAutoLocationToken(params) {
    return api.get(`${serviceDomin}/cus/${version}/api/review/token/gettoken`, params);
  },
  /**
   * 获取用户是否登录信息
   */
  getIsLogin: function(params, cb) {
    return api.post(`${serviceDomin}/cus/${version}/api/msite/login/isLogin`, params);
  },
  /**
   * 获取我的车库列表
   */
  getMyCarList(params) {
    return api.post(`${serviceDomin}/cus/${version}/api/car/review/getMyCarList`, params);
  },
  /**
   * 获取用户个人信息
   */
  getUserInfo(params) {
    return api.get(`${serviceDomin}/v2/api/users/${params.userId}/info/base`);
  },
  /**
   * 预约单列表
   */
  getReservationsList: (params) => {
    return api.get(`${serviceDomin}/v2/api/transparent/order/myOrderList`, params);
  },
  /**
   * 预约单详情
   */
  getReservationsDetail: (params) => {
    return api.get(`${serviceDomin}/v2/api/transparent/order/detail/${params.orderId}`);
  },
  /**
   * 取消预约单
   */
  getCancelOrder: (params) => {
    return api.post(`${serviceDomin}/v2/api/transparent/order/cancel`, params);
  },
  /**
   * 获取服务进程追踪
   */
  getProcessTracking: (params) => {
    return api.get(`${serviceDomin}/v2/api/transparent/progress/timeLineList/${params.orderId}`);
  },
  /**
   * 商家详情
   */
  getDealerDetail: (params = {}) => {
    return api.get(`${serviceDomin}${versionV2}dealer/transparentsrv/dealerDetail`, params);
  },
  /**
   *  获取顾问咨询地址接口
   */
  getDealerStaffs: (params = {}) => {
    return api.get(`${serviceDomin}${versionV2}transparent/dealerStaffs/getBestIMSales`, params);
  },
  /**
   *  获取门店技师接口
   */
  getDealerStaffsList: (params = {}) => {
    return api.get(`${serviceDomin}${versionV2}transparent/dealerStaffs/list`, params);
  },
  /**
   *  商家详情单条点评
   */
  getReviewList: (params = {}) => {
    return api.get(`${serviceDomin}${versionV2}reviews/transparentservice/review/single`, params);
  },
  /**
   * 商家列表筛选项
   */
  getDealerListFilters: (params) => {
    return api.post(`${serviceDomin}/cus/${version}/api/review/dealer/transparentservice/filter`, params);
  },
  /**
   * 获取维修预约详情信息、套餐预约详情信息
   */
  getRepairOrPackageDetail: (params) => {
    return api.get(`${serviceDomin}/v2/api/transparent/maintain/detail`, params);
  },
  /**
   * 获取保养预约详情信息
   */
  getMaintainDetail: (params) => {
    return api.get(`${serviceDomin}/v2/api/transparent/maintain/item`, params);
  },
  /**
   * 提交订单
   */
  addOrder: (params) => {
    return api.post(`${serviceDomin}/v2/api/transparent/order/add`, params);
  },
  /**
   * 获取商家列表
   */
  getDealerList: (params) => {
    return api.post(`${serviceDomin}/v2/api/dealers/transparentservice/dealerlist`, params);
  },
  /**
   * 保养选择配件数据初始化接口
   */
  getMaintainInit: (params = {}) => {
    return api.get(`${serviceDomin}/v2/api/transparent/maintain/init`, params);
  },
  /**
   * 保养选择配件数据详情接口
   */
  getMaintainAdvice: (params = {}) => {
    return api.get(`${serviceDomin}/v2/api/transparent/maintain/advice`, params);
  }
};
